export default {
	name: 'PublishHouseInfo',
	data() {
		return {
			selectDate: '具体时间',
			directions: [
				{
					title: '朝东',
					isCheck: false
				},
				{
					title: '朝西',
					isCheck: false
				},
				{
					title: '朝南',
					isCheck: false
				},
				{
					title: '朝北',
					isCheck: false
				}
			],
			configs: [
				{
					title: '热水器',
					isCheck: false
				},
				{
					title: '冰箱',
					isCheck: false
				},
				{
					title: '空调',
					isCheck: false
				},
				{
					title: '暖气',
					isCheck: false
				},
				{
					title: '洗衣机',
					isCheck: false
				},
				{
					title: '电视机',
					isCheck: false
				},
				{
					title: '燃气灶',
					isCheck: false
				},
				{
					title: 'WIFI',
					isCheck: false
				},
				{
					title: '独立卫生间',
					isCheck: false
				},
				{
					title: '大阳台',
					isCheck: false
				},
				{
					title: '微波炉',
					isCheck: false
				},
				{
					title: '衣柜',
					isCheck: false
				}
			],
			feature: [
				{
					title: '地铁房',
					isCheck: false
				},
				{
					title: '电梯房',
					isCheck: false
				},
				{
					title: '可短租',
					isCheck: false
				}
			],
			expects: [
				{
					title: '一个人住',
					isCheck: false
				},
				{
					title: '不吸烟',
					isCheck: false
				},
				{
					title: '不能养宠物',
					isCheck: false
				},
				{
					title: '爱干净',
					isCheck: false
				},
				{
					title: '比较安静',
					isCheck: false
				},
				{
					title: '作息规律',
					isCheck: false
				},
				{
					title: '工作稳定',
					isCheck: false
				},
				{
					title: '仅限女生',
					isCheck: false
				},
				{
					title: '仅限男生',
					isCheck: false
				}
			],
			decorations: [
				{
					title: '毛坯',
					isCheck: false
				},
				{
					title: '简单装修',
					isCheck: false
				},
				{
					title: '精装修',
					isCheck: false
				},
				{
					title: '豪华装修',
					isCheck: false
				}
			],
			time: [
				{
					title: '随时入住',
					isCheck: false
				}
			]
		}
	},
	methods: {
		directionsClick (index) {
			this.directions.forEach((it, i) => {
				it.isCheck = index === i
			})
		},
		decorationsClick (index) {
			this.decorations.forEach((it, i) => {
				it.isCheck = index === i
			})
		},
		timeClick (index) {
			this.time[index].isCheck = true
			this.selectDate = '具体时间'
		},
		timeChange (e) {
			this.time[0].isCheck = false
			const selectDate = new Date(e.detail.value)
			if (selectDate < new Date()) {
				this.$toast('所选日期有点小哦')
				return
			}
			this.selectDate = e.detail.value
		},
		checkItem (item) {
			item.isCheck = !item.isCheck
		},
		confirm () {
			const selectDirection = this.directions.filter(it => {
				return it.isCheck
			})
			const selectConfigs = this.configs.filter(it => {
				return it.isCheck
			})
			const selectFeature = this.feature.filter(it => {
				return it.isCheck
			})
			const selectException = this.expects.filter(it => {
				return it.isCheck
			})
			const selectDecoration = this.decorations.filter(it => {
				return it.isCheck
			})
			let enterTime = null
			if (this.time[0].isCheck) {
				enterTime = '随时入住'
			} else if (this.selectDate !== '具体时间'){
				enterTime = this.selectDate
			}
			const selected = {
				'direction': selectDirection.map(it => it.title),
				'configs': selectConfigs.map(it => it.title),
				'feature': selectFeature.map(it => it.title),
				'exception': selectException.map(it => it.title),
				'decoration': selectDecoration.map(it => it.title),
				'time': enterTime ? [enterTime] : []
			}
			uni.$emit('selectedHouseInfo', selected)
			this.$back()
		}
	}
}
